* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Cairo', sans-serif;
  font-size: 10px;
}

.wrapper {
  margin: 10vh auto;
  width: 80vw;
  max-width: 1000px;
  height: 80vh;
  /* background-color: rgb(154, 154, 185); */
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

@media (max-width: 1024px) {
  .wrapper {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .wrapper {
    justify-content: space-evenly;
  }
}
